import { createWebHistory, createRouter } from "vue-router";
import m_auth from "@/components/m_auth.vue";
import m_mess from "@/components/m_mess.vue";
import m_feed from "@/components/m_feed.vue";
import m_404 from "@/components/m_404.vue";
import m_user_edit from "@/components/m_user_edit.vue";

const routes = [
  {
    path: "/auth",
    name: "Home",
    component: m_auth,
  },
  {
    path: "/m",
    name: "MEss",
    component: m_mess,
  },
  {
    path: "/",
    name: "About",
    component: m_feed,
  },
  {
    path: "/user/",
    name: "User Edit",
    component: m_user_edit,
  },
  { path: '/:pathMatch(.*)*', component: m_404 },
]
const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;